
.tasks > p{
    text-align: center;
    font-size: calc(5px + 2vmin);
}

.active {
    border-left: 6px solid rgb(34, 34, 34);
    background-color: blue;
}



#task-input[type=text] {
    width: 100%;
    padding: 12px 20px;
    margin: 8px 0;
    box-sizing: border-box;
    border: none;
    border-bottom: 2px solid white;
    background-color:#db524d;
    color: white;
    font-size: calc(6px + 2vmin);

}

.task {
    width: 100%;
    background-color: white;
    color: #db524d;
    font-size: calc(8px + 2vmin);
    border-radius: 4px;
    cursor: pointer;
    margin-top: 5px;
    text-align: left;
    box-sizing: border-box;
    transition: unset;
    box-shadow: none;
    transform: translateY(2px);
    
}

.task > p {
    padding-top: 4px;
    padding-bottom: 4px;
    padding-left: 15px; 

}



.add-button
{
    width: 100%;
    height: 60px;
    background-color: rgba(0, 0, 0, 0.1);
    border-radius: 8px;
    display: flex;
    -webkit-box-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    align-items: center;
    cursor: pointer;
    opacity: 0.8;
    margin-top: 12px;
    border: 2px dashed rgba(255, 255, 255, 0.7);
    color: white;
    font-size: calc(6px + 2vmin);

}